<template>
  <div class="Palette">
    <span :style="`background:rgba(${this.vibrant})`"></span>
    <span :style="`background:rgba(${this.muted})`"></span>
    <span :style="`background:rgba(${this.darkVibrant})`"></span>
    <span :style="`background:rgba(${this.darkMuted})`"></span>
    <span :style="`background:rgba(${this.lightVibrant})`"></span>
    <span :style="`background:rgba(${this.lightMuted})`"></span>
  </div>
</template>

<script>
export default {
  props: {
    colors: {
      type: Object,
      required: true
    }
  },
  computed: {
    vibrant() {
      const vibrant = this.colors.Vibrant;
      return vibrant ? vibrant.rgb : "(0, 0, 0, 0)";
    },
    muted() {
      const muted = this.colors.Muted;
      return muted ? muted.rgb : "(0, 0, 0, 0)";
    },
    darkVibrant() {
      const darkVibrant = this.colors.DarkVibrant;
      return darkVibrant ? darkVibrant.rgb : "(0, 0, 0, 0)";
    },
    darkMuted() {
      const darkMuted = this.colors.DarkMuted;
      return darkMuted ? darkMuted.rgb : "(0, 0, 0, 0)";
    },
    lightVibrant() {
      const lightVibrant = this.colors.LightVibrant;
      return lightVibrant ? lightVibrant.rgb : "(0, 0, 0, 0)";
    },
    lightMuted() {
      const lightMuted = this.colors.LightMuted;
      return lightMuted ? lightMuted.rgb : "(0, 0, 0, 0)";
    }
  },
  mounted() {
    console.log(this.colors);
  }
};
</script>
