.Palette {
  position: fixed;
  bottom: 30px;
  background-color: white;
  display: flex;
  justify-content: space-between;
  width: 60vw;
  padding: 15px 20px;
  box-sizing: border-box;
  left: 50%;
  transform: translateX(-50%);
}
span {
  width: 6vw;
  height: 6vw;
  max-width: 30px;
  max-height: 30px;
  border-radius: 50%;
  display: block;
  transition: all 0.2s;
}
</style>
