<template>
  <div class="CameraView">
    <video playsinline autoplay></video>
    <canvas :width="width" :height="height"></canvas>
    <!-- <span class="Focuspoint"></span> -->
    <ColorPalette :colors="palette" />
  </div>
</template>

<script>
import Vibrant from "node-vibrant";
import ColorPalette from "./ColorPalette";

export default {
  name: "CameraView",
  components: {
    ColorPalette
  },
  props: {
    msg: String
  },
  data: () => ({
    height: "",
    width: "",
    palette: {},
    canvas: null
  }),
  methods: {
    async initCamera() {
      const video = document.querySelector("video");
      const canvas = document.querySelector("canvas");
      const stream = await this.getCameraStream();

      video.srcObject = stream;
      this.video = video;
      this.canvas = canvas;

      return video;
    },
    async getCameraStream() {
      let stream = null;
      const constraints = {
        audio: false,
        video: {
          facingMode: "environment",
          width: window.outerWidth,
          height: window.outerheight
        }
      };
      try {
        stream = await navigator.mediaDevices.getUserMedia(constraints);
      } catch (err) {
        console.log(err);
      }
      return stream;
    },
    async cameraImageToCanvas(video) {
      // this.width = video.videoWidth;
      // this.height = video.videoHeight;
      window.requestAnimationFrame(() => {
        this.canvas.getContext("2d").drawImage(video, 0, 0);
      });
    },
    imageData() {
      const ctx = this.canvas.getContext("2d");
      const image = new Image();
      let base64 = "";

      image.src = this.canvas.toDataURL("image/jpeg");
      ctx.filter = "contrast(1.2)";
      ctx.drawImage(image, 0, 0);
      base64 = this.canvas.toDataURL("image/jpeg");

      return base64;
    },
    async extractColorsFromCanvas() {
      const options = {
        quantizer: Vibrant.Quantizer.WebWorker,
        quality: 1
      };
      const imageData = this.imageData();
      const palett = await Vibrant.from(imageData, options).getPalette();
      return palett;
    },
    async update() {
      await this.cameraImageToCanvas(this.video);
      this.palette = await this.extractColorsFromCanvas();

      setTimeout(() => {
        window.requestAnimationFrame(this.update);
      }, 500);
    }
  },
  mounted: function() {
    this.initCamera().then(() => this.update());
  }
};
</script>

